			regionCode2: 0,
			regionCode3: 0,
			regionCode4: 0,
			regionCode5: 0,
			userPostCode1: 1,
			userPostCode2: 1,
			userPostCode3: 1,
			userPostCode4: 1,
			userPostCode5: 1,
			percent: 65,
			content1:'',
			content2:'',
			content3: '',
			content4:'',
			content5:'',
