			        age: '21',
			        hobby: '绘画'
			    },
			    {
			        id: 2,
			        name: '王二',
			        age: '29',
			        hobby: '滑板'
			    },
			    {
			        id: 3,
			        name: '码字',
			        age: '20',
			        hobby: '蹦极'
			    }
			]
		};
	},
