		this.postCode = uni.getStorageSync('pupDefault')
		this.fetchList1()
		this.fetchList2()
		this.fetchList3()
		this.fetchList4()
		this.fetchList5()
	},
	methods:{
		async fetchList1(){
			
			let data = {
