				endTime	: this.selectTime.time4.endTime	//结束时间
		
			}
			console.log(data)
			const res = await this.$http.get('/data/latent',{data: data})
			console.log(res)
			this.content4 = res.data.data
			
		},
		async fetchList5(){
			
